import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const Kazuma = () => import('../views/kazuma/Kazuma')
const Story = () => import('../views/story/Story')
const Fans = () => import('../views/fans/Fans')
const More = () => import('../views/more/More')
const routes = [
  {
    path: '',
    redirect: '/kazuma'
  },
  {
    path: '/kazuma',
    component: Kazuma
  },
  {
    path: '/story',
    component: Story
  },
  {
    path: '/fans',
    component: Fans
  },
  {
    path: '/more',
    component: More
  },
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router

