import {
  ADD_COUNTER,
  ADD_TO_CART
} from "@/store/mutation-types";

export default {
  // actions里面函数的第一个参数为context
  addCart(context, payload) {
   return new Promise(((resolve, reject) => {
     let oldproduct = context.state.cartList.find(item => item.iid === payload.iid)
     if (oldproduct) {
       //Action 中的回调函数会接收一个上下文 context 对象，它包含了当前 store 中所有属性和方法，但其不是 store 本身。
       //可以通过 context.commit 来提交 mutation
       context.commit(ADD_COUNTER,oldproduct)
       resolve('商品数量+1')
     } else {
       payload.count = 1
       context.commit(ADD_TO_CART,payload)
       resolve('添加新商品')
     }
   }))
  }
}
